import { Component, Inject } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService } from '@tei/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { firstValueFrom, Subscription } from 'rxjs';
import { IHostAdminCenterHelperService } from '../../../@intermediate/host-admin-center-helper.interface';
import { GetSelectPiSetupResponse, SelectPiSetupForm } from '../../../@intermediate/models/get-select-pi-setup.model';
import { portalGroupDetails } from '../../../@intermediate/models/host-admin-center-helper.model';
import { PostSelectPiSetupRequest } from '../../../@intermediate/models/post-select-pi-setup.model';
import { ISelectPiSetupService } from '../../../@intermediate/select-pi-setup.interface';

@Component({
  selector: 'tei-admin-admin-center-select-pi-setup',
  templateUrl: './select-pi-setup.component.html',
  styleUrls: ['./select-pi-setup.component.css']
})
export class SelectPiSetupComponent
{
  breadCrumbData: BreadCrumb[] = [];
  subscription: Subscription = new Subscription();
  portalDetail: portalGroupDetails;
  hidePassword = true;
  settingForm = new FormGroup<SelectPiSetupForm>({
    SetupID: new FormControl<number>(0),
    ExpiryHours: new FormControl<number>(24, Validators.required),
    TenantName: new FormControl<string>('', Validators.required),
    TenantID: new FormControl<string>('', Validators.required),
    UserName: new FormControl<string>('', Validators.required),
    PassKey: new FormControl<string>('', Validators.required),
    ApiUrl: new FormControl<string>('', [Validators.required, Validators.pattern(/^(https?:\/\/.+)$/)]),
    QRCodePrefix: new FormControl<string>('', Validators.required),
    IsActive: new FormControl<boolean>(false),
    PrintQRCode: new FormControl<boolean>(true),
    ExcludeLeadingZero: new FormControl<boolean>(false),
  });
  constructor(
    @Inject('IHostAdminCenterHelperService') private hostAdminCenterHelperService: IHostAdminCenterHelperService,
    @Inject("ISelectPiSetupService") private selectPiSetupService: ISelectPiSetupService,
    private snackBar: SnackBarService,
    private translate: TranslateService,
    public manageLoadingService: ManageLoadingService)
  {

  }

  ngOnInit(): void
  {
    this.subscription.add(this.hostAdminCenterHelperService.currentPortalGroupDetails.subscribe(portalDetail =>
    {
      if (portalDetail)
      {
        this.portalDetail = portalDetail;
        this.fillBreadCrump(portalDetail);
        this.loadData();
      }
    }));

  }

  async loadData(): Promise<void>
  {
    try
    {
      const response = await firstValueFrom(this.selectPiSetupService.get({ PortalID: this.portalDetail.portal.PortalID }));
      this.initForm(response);

    }
    catch (error)
    {
      console.error(error);
    }
    finally
    {
      this.manageLoadingService.finishLoading();
    }

  }

  initForm(response: GetSelectPiSetupResponse): void
  {
    this.settingForm.patchValue({
      ExpiryHours: response?.ExpiryHours ?? 24,
      TenantName: response?.TenantName,
      TenantID: response?.TenantID,
      UserName: response?.UserName,
      PassKey: response?.PassKey,
      ApiUrl: response?.ApiUrl,
      QRCodePrefix: response?.QRCodePrefix,
      IsActive: response?.IsActive,
      PrintQRCode: response?.PrintQRCode,
      ExcludeLeadingZero: response?.ExcludeLeadingZero,
      SetupID: response.SetupID

    });
  }

  async saveSetting(): Promise<void>
  {
    try
    {
      const request: PostSelectPiSetupRequest = {
        ApiUrl: this.settingForm.value.ApiUrl,
        ExcludeLeadingZero: this.settingForm.value.ExcludeLeadingZero,
        ExpiryHours: this.settingForm.value.ExpiryHours,
        TenantName: this.settingForm.value.TenantName,
        TenantID: this.settingForm.value.TenantID,
        UserName: this.settingForm.value.UserName,
        PassKey: this.settingForm.value.PassKey,
        QRCodePrefix: this.settingForm.value.QRCodePrefix,
        IsActive: this.settingForm.value.IsActive,
        PrintQRCode: this.settingForm.value.PrintQRCode,
        PortalID: this.portalDetail.portal.PortalID,
        SetupID: this.settingForm.value.SetupID,
      };
      await firstValueFrom(this.selectPiSetupService.post(request));
      this.snackBar.showSuccess(this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.SELECT_PI_SETUP.SAVED_SUCCESSFULLY'), '', null, 'left');
    }
    catch (error)
    {
      console.error(error);
    }
    finally
    {
      this.manageLoadingService.finishLoading();
    }
  }

  fillBreadCrump(portalDetail): void
  {
    this.breadCrumbData = [
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
        relativeUrl: '../../../../../../',
      },
      {
        title: `${ portalDetail.parentPortalGroup.PortalGroup.PortalGroupName }`,
        relativeUrl: `../../../../../`,
      },
      {
        title: `${ portalDetail.portal.PortalName }`,
        relativeUrl: '../../../../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ACCESS'),
        relativeUrl: '../../../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.INTEGRATION.TITLE'),
        relativeUrl: '../../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.RANGEMACHINEINTEGRATION'),
        relativeUrl: '../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.SELECT_PI_SETUP.TITLE'),
        relativeUrl: '',
      }
    ];
  }
}
