import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetSelectPiSetupRequest, GetSelectPiSetupResponse } from '../@intermediate/models/get-select-pi-setup.model';
import { PostSelectPiSetupRequest, PostSelectPiSetupResponse } from '../@intermediate/models/post-select-pi-setup.model';
import { ISelectPiSetupService } from '../@intermediate/select-pi-setup.interface';

@Injectable({
  providedIn: 'root'
})
export class SelectPiSetupService implements ISelectPiSetupService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/DeviceIntegration/RangeServantIntegration';
  }
  get(request: GetSelectPiSetupRequest): Observable<GetSelectPiSetupResponse>
  {
    const apiUrl = this.serviceUrl + '/GetGRangePi';
    return this.http.get<GetSelectPiSetupResponse>(apiUrl, { params: request as any }
    );
  }
  post(request: PostSelectPiSetupRequest): Observable<PostSelectPiSetupResponse>
  {
    const apiUrl = this.serviceUrl + '/PostGRangePi';
    return this.http.post<PostSelectPiSetupResponse>(apiUrl, request);
  }
}
